/* eslint-disable no-return-assign */
<template>
    <div>
        <div style="max-width: 800px; margin: 0 auto; display: flex; align-items: center; justify-content: space-between">
            <div>
                <h1>   {{infoUser.username}}  coordinates:</h1>
                <div v-if="!infoUser.lat">
                    <p>not found</p>
                </div>
                <div v-else>
                    <p>{{ infoUser.lat }} Latitude, {{ infoUser.lng }} Longitude</p>
                </div>
            </div>
            <div>
                <h1>Map coordinates:</h1>
                <p>{{ mapCoordinates.lat }} Latitude, {{ mapCoordinates.lng }} Longitude</p>
            </div>
        </div>
        <GmapMap
            :center="myCoordinates"
            :zoom="zoom"
            style="width:640px; height:360px; margin: 32px auto;"
            ref="mapRef"
            @dragend="handleDrag"
        ></GmapMap>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      id: this.$route.query.id,
      map: null,
      myCoordinates: {
        lat: 0,
        lng: 0
      },
      zoom: 7,
      infoUser: ''
    }
  },
  created () {
    // does the user have a saved center? use it instead of the default
    if (localStorage.center) {
      this.myCoordinates = JSON.parse(localStorage.center)
    } else {
      // get user's coordinates from browser request
      this.$getLocation({})
        .then(coordinates => {
          this.myCoordinates = coordinates
        })
        .catch(error => alert(error))
    }

    // does the user have a saved zoom? use it instead of the default
    if (localStorage.zoom) {
      this.zoom = parseInt(localStorage.zoom)
    }
  },
  mounted () {
    // add the map to a data object
    // eslint-disable-next-line no-return-assign
    this.$refs.mapRef.$mapPromise.then(map => this.map = map)
    this.detaiMap(this.id).then(res => {
      this.infoUser = res.data[0]
      const data = JSON.parse(this.infoUser.location)
      this.latestLocation = data
    })
  },
  methods: {
    handleDrag () {
      // get center and zoom level, store in localstorage
      const center = {
        lat: this.map.getCenter().lat(),
        lng: this.map.getCenter().lng()
      }
      const zoom = this.map.getZoom()

      localStorage.center = JSON.stringify(center)
      localStorage.zoom = zoom
    },
    ...mapActions({
      addLocation: 'map/addLocationInDatabse',
      detaiMap: 'map/detailMap'
    }),
    inserTlocation (param) {
      const data = {
        lat: this.myCoordinates.lat,
        lng: this.myCoordinates.lng
      }
      this.addLocation(data)
    }
  },
  computed: {
    mapCoordinates () {
      if (!this.map) {
        return {
          lat: 0,
          lng: 0
        }
      }

      return {
        lat: this.map.getCenter().lat().toFixed(4),
        lng: this.map.getCenter().lng().toFixed(4)
      }
    }
  }
}
</script>
