<template>
  <div class="change-bg">
      <div class="container rounded mt-5">
    <div class="row">
        <div class="col-md-4 border-right">
            <div class="d-flex flex-column circular--potrait align-items-center text-center p-3 py-5"><img class=" mt-5" :src="`http://localhost:3000/${form.image}`" width="90">
                <label class="fileContainer mt-2">
                    choose photo
                    <input name="image" @change="uploadfile"  type="file"/>
                </label>
            <span class="font-weight-bold">{{form.username}}</span>
            <span class="text-black-50">{{form.email}}</span>
            <span class="text-black-50">{{form.phone_number}}</span>
            <span>{{form.country}}</span>
            </div>
        </div>
        <div class="col-md-8">
            <div class="p-3 py-5">
                <div class="d-flex justify-content-between align-items-center mb-3">
                    <div class="d-flex flex-row align-items-center back"><b-icon icon="arrow-left" scale="2"></b-icon>
                    </div>
                    <h6 class="text-right">{{form.images}}</h6>
                </div>
               <form @submit.prevent="editData" >
                    <div class="row mt-2">
                    <div class="col-md-6"><input name="username" v-model="form.username" type="text" class="form-control" placeholder="username" ></div>
                    <div class="col-md-6"><input name="alamat" type="text" v-model="form.alamat" class="form-control"  placeholder="Alamat"></div>
                </div>
                <div class="row mt-3">
                    <div class="col-md-6"><input  name="email" readonly type="text" v-model="form.email" class="form-control" placeholder="Email" ></div>
                    <div class="col-md-6"><input name="phone_number" type="text" v-model="form.phone_number" class="form-control" placeholder="Phone number"></div>
                </div>
                <div class="row mt-3">
                    <div class="col-md-6"><input type="text" class="form-control"  v-model="form.country" placeholder="Country"></div>
                    <div class="col-md-6">  <input type="file" @change="uploadfile"></div>
                </div>
                <div class="mt-5 text-right"><button class="btn btn-primary profile-button" type="submit">Save Profile</button></div>
               </form>
            </div>
        </div>
    </div>
</div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      allData: [],
      form: {
        username: '',
        email: '',
        alamat: '',
        phone_number: '',
        country: '',
        address: '',
        files: '',
        image: ''
      }
    }
  },
  computed: {
    ...mapActions({
      data: 'profile/getAlluser'
    })
  },
  methods: {
    ...mapActions({
      dataAll: 'profile/getAlluser',
      updateData: 'profile/updateUser'
    }),
    uploadfile: function (e) {
      this.form.files = e.target.files[0]
    },
    editData () {
      const formdata = new FormData()
      // console.log(this.form.files)
      formdata.append('username', this.form.username)
      formdata.append('alamat', this.form.alamat)
      formdata.append('phone_number', this.form.phone_number)
      formdata.append('country', this.form.country)
      formdata.append('address', this.form.address)
      formdata.append('image', this.form.files)
      this.updateData(formdata).then(dt => {
        alert(dt)
        window.location = '/user'
      }).catch(dt => {
        console.log(dt)
        alert(dt)
      })
    }
  },
  mounted () {
    this.dataAll().then(data => {
      this.allData = data
      this.form.username = data[0].username
      this.form.email = data[0].email
      this.form.phone_number = data[0].phone_number
      this.form.country = data[0].country
      this.form.alamat = data[0].alamat
      this.form.image = data[0].image
    })
  }
}
</script>

<style scoped>
img {
  max-width: 30%;
  width: 100%;
  margin-right: 20px;
  border-radius: 50%;
}

.form-control:focus {
    box-shadow: none;
    border-color: #BA68C8
}
.fileContainer {
    overflow: hidden;
    position: relative;
    background: pink;
}

.fileContainer [type=file] {
    cursor: inherit;
    display: block;
    font-size: 999px;
    filter: alpha(opacity=0);
    min-height: 100%;
    min-width: 100%;
    opacity: 0;
    position: absolute;
    right: 0;
    text-align: right;
    top: 0;
}

.profile-button {
    background: #BA68C8;
    box-shadow: none;
    border: none
}

.profile-button:hover {
    background: #682773
}

.profile-button:focus {
    background: #682773;
    box-shadow: none
}

.profile-button:active {
    background: #682773;
    box-shadow: none
}

.back:hover {
    color: #682773;
    cursor: pointer
}
.rounded {
    background: #fafafa;
}
</style>
