<template>
  <div class="content-wrap">
      <div class="islogin-wrap">
<form @submit.prevent="onRegis">
    <div class="isLogin mb-3">
        <h2 class="text-primary">Register</h2>
    </div>
    <div class="arraw-regis">
         <h4 @click="loginPath"><b-icon icon="arrow-left-short" scale="2" variant="info"></b-icon></h4>
    </div>
    <span class="mt-4">Hi, welcome to login</span>
        <div class="form-group mt-2">
            <label for="exampleFormControlInput1">name</label>
            <input v-model="form.username" type="text" class="form-control" id="exampleFormControlInput1" placeholder="name">
        </div>
        <div class="form-group mt-2">
            <label for="exampleFormControlInput1">Email</label>
            <input v-model="form.email" type="text" class="form-control" id="exampleFormControlInput1" placeholder="email">
        </div>
        <div class="form-group">
            <label for="exampleFormControlInput1">password</label>
            <input v-model="form.password" type="password" class="form-control" id="exampleFormControlInput1" placeholder="password">
        </div>
        <div class="buttoon-login text-center">
            <button class="btn-login" type="submit">Register</button>
        </div>
        <div class="forgot-pass">
            <p class="text-primary">forgot password </p>
        </div>
        <div class="login-with">
            <hr style="border: 1px solid black; width: 20%">
            <p>register with</p>
            <hr style="border: 1px solid black; width: 20%">
        </div>
 </form>
      </div>
  </div>
</template>

<script>
import Swal from 'sweetalert2'
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      form: {
        username: '',
        email: '',
        password: ''
      }
    }
  },
  computed: {
  },
  methods: {
    ...mapActions({
      regis: 'register/isregister'
    }),
    loginPath () {
      this.$router.push({ path: '/login' })
    },
    onRegis () {
      this.regis(this.form).then(res => {
        if (res.message === 'berhasil register, silahkan cek email') {
          Swal.fire(
            'Good job!',
            `${res.message}`,
            'success'
          )
          setTimeout(() => {
            window.location = '/login'
          }, 3000)
        } else {
          Swal.fire(
            'failed',
            `${res.message}`,
            'error'
          )
        }
      })
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
.content-wrap {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E5E5E5;
}
.arraw-regis {
    position: absolute;
    top: 150px;
    cursor: pointer;
}
.islogin-wrap{
    background: #FFFFFF;
    padding: 30px;
    box-shadow: 0px 20px 20px rgba(126, 152, 223, 0.05);
    width: 30%;
    height: 70vh;
    border-radius: 30px;
}
.isLogin {
    display:flex;
    justify-content: space-around;
}
.btn-login {
    padding: 7px;
    width: 90%;
    align-items: center;
    background: #7E98DF;
    border-radius: 80px;
    border: none;
}
.forgot-pass {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
    margin-right: 30px;
    cursor: pointer;
}
.login-with {
    display: flex;
    justify-content: space-around;
}
input[type=text] {
  width: 100%;
  padding: 12px 20px;
  margin: 1px 0;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid #D2C2FF;
}
input[type=password] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid #D2C2FF;
}

@media (max-width: 800px) {
  .islogin-wrap{
    width: 100%;
    height: 100vh;
}
.arraw-regis {
    position: absolute;
    top: 40px;
}
}

</style>
