import axios from 'axios'
const state = () => {
  return {
    data: ''
  }
}

const actions = {
  isregister ({ commit }, payload) {
    console.log(payload)
    return new Promise((resolve, reject) => {
      axios.post(`${process.env.VUE_APP_API_URL}/user/register`, payload)
        .then(ress => {
          resolve(ress.data)
        })
    })
  }
}

export default {
  namespaced: true,
  state,
  actions
}
