import axios from 'axios'
const state = () => {
  return {
    id: localStorage.getItem('id')
  }
}

const actions = {
  addLocationInDatabse: ({ commit, state }, payload) => {
    console.log(state)
    const parse = JSON.stringify(payload)
    console.log(parse)
    return new Promise((resolve, reject) => {
      axios.put(`http://18.208.165.238:4011/user/map/${state.id}`, {
        lat: payload.lat,
        lng: payload.lng

      }).then(res => {
        console.log(res)
      })
    })
  },
  detailMap: (contex, id) => {
    return new Promise((resolve, reject) => {
      axios.get(`http://18.208.165.238:4011/user/${id}`).then(dt => {
        resolve(dt.data)
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  actions
}
