import Vue from 'vue'
import Vuex from 'vuex'
import register from './register/index'
import profile from './profile/index'
import addFriend from './addFriend/index'
import map from './map/index'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    register,
    username: '',
    profile,
    addFriend,
    map
  },
  state: {
    token: localStorage.getItem('token') || null
  },
  getters: {
    islogin (state) {
      if (state.token !== null) {
        return true
      } else {
        return false
      }
    },
    getusername (state) {
      return state.username
    }
  },
  mutations: {
    send_username (state, payload) {
      state.username = payload.data.username
    }
  },
  actions: {
    islogin ({ commit }, payload) {
      return new Promise((resolve, reject) => {
        axios.post(`${process.env.VUE_APP_API_URL}/user/login`, payload).then(res => {
          if (res.data.message === 'berhasil login') {
            resolve(res.data.message)
            localStorage.setItem('token', res.data.tokenLogin)
            localStorage.setItem('username', res.data.username)
            localStorage.setItem('id', res.data.id)
          } else if (res.data.message === 'password salah') {
            resolve(res.data.message)
          } else if (res.data.message === 'email not found') {
            resolve(res.data.message)
          } else if (res.data.message === 'email not activated') {
            resolve(res.data.message)
          } else {
            resolve('internal server eror')
          }
          commit('send_username', res)
        }).catch(err => {
          reject(err)
        })
      })
    }
  }
})
